.sidebar {
  width: 300px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: #282828; }

.sidebar__nav {
  margin: 0;
  padding: 0;
  list-style: none; }

.sidebar__nav__item {
  margin-bottom: 39px;
  margin-right: 30px;
  margin-left: 30px;
  position: relative; }
  .sidebar__nav__item:hover, .sidebar__nav__item.active {
    color: #6863ce; }
  .sidebar__nav__item:before {
    position: absolute;
    top: 1px;
    left: 0;
    font-size: 24px; }
  .sidebar__nav__item:after {
    content: '';
    width: 6px;
    height: 6px;
    position: absolute;
    top: 9px;
    left: -33px;
    border-radius: 50%;
    opacity: 0;
    box-shadow: 0 0 10px 3px rgba(104, 99, 206, 0.32);
    background-color: #6863ce; }
  .sidebar__nav__item.active:after {
    opacity: 1; }

.sidebar__nav__item__link {
  padding-top: 3px;
  padding-bottom: 1px;
  padding-left: 37px;
  display: block; }

.sidebar__btn {
  font-size: 0;
  position: absolute;
  top: 27px;
  right: -48px;
  cursor: pointer; }
  .sidebar__btn:hover {
    opacity: 0.5; }
  .sidebar__btn:before {
    font-size: 16px; }
